import Vue from 'vue';
import App from '@/App';
import Router from 'vue-router'
import QuestionAnswerDetails from '@/view/comment/comment-question-answer-details.vue'; //问答详情

import 'html5-reset/assets/css/reset.css'

Vue.config.productionTip = false
Vue.use(Router)

var router = new Router({
  // mode: 'history',
  // base: '/question-answer-details.html',
  routes: [
    {
      path: '/',
      name: 'QuestionAnswerDetails',
      component: QuestionAnswerDetails
    }
  ]
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})


